import React from 'react';
import MediaQuery from 'react-responsive';
import HamburgerMenu from 'react-hamburger-menu';
import Logo from '../assets/img/logo.png';
import { TextButton, PhoneButton, LineButton } from './Button';
import { Link } from "react-router-dom";
import LineIcon from '../assets/img/social/line.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPhoneAlt } from '@fortawesome/free-solid-svg-icons'
import { Map } from '../components/Content';
import { withRouter } from 'react-router-dom'


class Navbar extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			open: false
		};
	}

	async scrollTo(id){
		let el = await document.getElementById(id);
	
		if (el == null){
			await this.props.history.push("/");
			el = document.getElementById(id)
		}

		if (el != null) {
			el.scrollIntoView({
				behavior: 'smooth'
			});
		}

		this.resetMenu()
	}

	resetMenu(){
		this.setState({
			open: false
		});
	}

	openMenu() {
		this.setState({
			open: !this.state.open
		});
	}

	render() {
		return (
			<div>
				<div className='navbar'>
					<div className='navbar__container'>
						<Link to='/' onClick={this.resetMenu.bind(this)}>
							<img src={Logo} alt='Logo' className='navbar__logo'/>
						</Link>

						<MediaQuery minWidth={1025}>
							<div className='navbar__buttons'>
								<TextButton 
									slot={
										<Link to='#' className='text__18' onClick={this.scrollTo.bind(this, 'why_choose_us')}>ทำไมต้องเลือกเรา?</Link>
									}
								/>

								<TextButton 
									slot={
										<Link to='/about' className='text__18'>เกี่ยวกับเรา</Link>
									}
								/>

								<TextButton 
									slot={
										<a href='https://medium.com/caratmthailand' target="_blank" rel="noopener noreferrer" className='text__18'>บทความ</a>
									}
								/>	

								<TextButton 
									slot={
										<Link to='/' className='text__18'>บริการอื่นๆ</Link>
									}
								/>
							</div>
							<div className='navbar__contacts'>
								<LineButton style={{marginRight: 15}}/>
								<PhoneButton/>
							</div>
						</MediaQuery>

						<MediaQuery maxWidth={1024}>
							<div className='navbar__contacts'>
								<HamburgerMenu
									isOpen={this.state.open}
									menuClicked={this.openMenu.bind(this)}
									width={30}
									height={20}
									strokeWidth={4}
									rotate={0}
									color='#244084'
									borderRadius={5}
									animationDuration={0.5}
									style={{margin: 'auto'}}
								/>
							</div>
						</MediaQuery>

					</div>
				</div>
				
				<MediaQuery maxWidth={1025}>
					<div className={"m-navbar__menu " + (this.state.open ? 'active' : '')}>
						<Link className='m-navbar__button' to='#' onClick={this.scrollTo.bind(this, 'why_choose_us')}>
							ทำไมต้องเลือก CAR ATM ?
						</Link>
						
						<Link className='m-navbar__button' to='/about' onClick={this.resetMenu.bind(this)}>
							เกี่ยวกับเรา
						</Link>

						<a href='https://medium.com/caratmthailand' target="_blank" rel="noopener noreferrer" className='m-navbar__button' onClick={this.resetMenu.bind(this)}>
							บทความเกี่ยวกับไฟแนนซ์รถ
						</a>

						<Link className='m-navbar__button' to='/' onClick={this.resetMenu.bind(this)}>
							บริการอื่นๆ
						</Link>
						
						<div className="m-navbar__contact__container">
							<a 
								href="tel:+6620791081"
								className='m-navbar__contact'
								style={{marginTop: 'auto', fontFamily: 'Sarabun', fontWeight: 'bold', paddingBottom: 0, color:'white'}}
								to='/'
							>
								020-791-801
								<div className='m-navbar__icon' style={{backgroundColor: '#fff'}}>
									<FontAwesomeIcon icon={faPhoneAlt}/>
								</div>
							</a>
							<a
								href='https://line.me/R/ti/p/%40189tkxzc' target="_blank" rel="noopener noreferrer" 
								className='m-navbar__contact' 
								to='/'
							>
								ติดต่อผ่านไลน์
								<div className='m-navbar__icon' style={{backgroundColor: '#00B900'}}>
									<img src={LineIcon}  alt='Line' />
								</div>
							</a>
						</div>
						
						{/* <div style={{flex: 1}}> */}
						<Map height='20vh'/>
						{/* </div> */}
					</div>
				</MediaQuery>

			</div>
		);
	}
}

export default withRouter(Navbar);