import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import Menu from '../components/Menu'
import MediaQuery from 'react-responsive';


export default class Home extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		return (
			<div className='container'>
				<Header
					style={{marginBottom: 20}}
					slot={
						<div className='largest__text'>
							<MediaQuery maxWidth={768}>
								<div>
									<span className='dark__blue'>จัดไฟแนนซ์</span>
									<span className='blue'>รถมือสอง</span>
								</div>
								<div>
									<span className='dark__blue'>ครบวงจร</span>
								</div>
							</MediaQuery>
							<MediaQuery minWidth={769}>
								<div>
									<span className='dark__blue'>จัดไฟแนนซ์</span>
									<span className='blue'>รถมือสอง</span>
									<span className='dark__blue'>ครบวงจร</span>
								</div>
							</MediaQuery>
						</div>
					}
				/>
				<Menu/>
				<WhyChooseUs/>
				<ContactUs/>
				<AboutUs/>
			</div>
		);
	}
}
