import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import GetStarted from '../components/GetStarted'
import GetStartedImage1 from '../assets/img/content/get_start_1.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/finance.png';

export default class Finance extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		return (
			<div className='container'>
				<Header
					slot={
						<div>
							<div className='largest__text'>
								<div>
									<span className='dark__blue'>จัดไฟแนนซ์</span>
									<span className='blue'>รถบ้าน</span>
								</div>
								<div>
									<span className='blue'>รถมือสอง</span>
								</div>
							</div>

							<img src={HeaderImage} alt='Header' className='header__image'/>
						</div>
					}
				/>

				<GetStarted
					getStartedData={
						[{
							image: GetStartedImage1,
							headerText:
								<div>
									<span className='dark__blue'>1. </span>
									<span className='blue'>ตกลงราคาซื้อขาย</span>
									<span className='dark__blue'>กับผู้ขายรถ</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>ตกลงราคาซื้อขายกับผู้ขายรถ</span>,
							bodyText: 
								<span className='dark__blue'>
									<font className='text__bold'>ติดต่อผู้ขายรถ</font> สรุปราคาที่เราพอใจ<br></br>
									โดยทั่วไป รถแต่ละรุ่นมีราคาที่ตลาดซื้อขายกัน<br></br>
									ถ้าเราได้ราคาถูกกว่าตลาด อาจจะไม่ต้องดาวน์เลยก็ได้ครับ
								</span>
						},{
							image: GetStartedImage2,
							headerText:
								<div>
									<span className='dark__blue'>2. </span>
									<span className='blue'>ติดต่อบริษัทไฟแนนซ์รถ</span>
									<span className='dark__blue'>เพื่อสรุปวงเงิน</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>ติดต่อบริษัทไฟแนนซ์รถเพื่อสรุปวงเงิน</span>,
							bodyText: 
								<span className='dark__blue'>
									ติดต่อหาบริษัทไฟแนนซ์รถ<br></br>
									เพื่อประเมินวงเงิน และ ดอกเบี้ยในการจัดไฟแนนซ์รถ <br></br>
									ปัจจัยของวงเงินและดอกเบี้ยจะขึ้นกับ<br></br>
									<font className='text__bold'>รถของคุณ</font>, <font className='text__bold'>วงเงินที่ต้องการ</font>, <font className='text__bold'>รายได้ของผู้จำนำ</font>
								</span>
						},{
							image: GetStartedImage3,
							headerText:
								<div>
									<span className='dark__blue'>3. จัดเตรียม</span>
									<span className='blue'>เอกสาร</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>จัดเตรียมเอกสาร</span>,
							bodyText: 
								<span className='dark__blue'>
									จัดเตรียมเอกสารต่างๆให้พร้อม<br></br>
									เตรียมยื่นให้บริษัทไฟแนนซ์รถ เช่น สมุดทะเบียนรถ,<br></br>
									<font className='text__bold'>บัตรประชาชน</font>, <font className='text__bold'>ทะเบียนบ้าน</font>, <font className='text__bold'>เอกสารแสดงรายได้ย้อนหลัง</font> เป็นต้น<br></br>
									ยิ่งพร้อมเร็ว ยิ่งผ่านเร็วครับ
								</span>
						},{
							image: GetStartedImage4,
							headerText:
								<div>
									<span className='dark__blue'>4. </span>
									<span className='blue'>นัดเซ็นสัญญา </span>
									<span className='dark__blue'>และ</span>
									<span className='blue'>โอนเล่มทะเบียนรถ</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>นัดเซ็นสัญญา และ โอนเล่มทะเบียนรถ</span>,
							bodyText: 
								<span className='dark__blue'>
									หลังจากเอกสารพร้อม <font className='text__bold'>บริษัทไฟแนนซ์นัดเซ็นสัญญา</font> เมื่อเซ็นเรียบร้อย<br></br>
									นัดโอนเล่มทะเบียนรถ และรับรถไปขับได้ทันที (ใช้เวลา2-3วัน)
								</span>
						}]
					}
					
				/>
				<WhyChooseUs/>
				<ContactUs/>
				<AboutUs/>
			</div>
		);
	}
}
