import React, { useState } from 'react';

import { Card } from '../components/Card';
import Swiper from 'react-id-swiper';
import MediaQuery from 'react-responsive';
import LeftArrow from '../assets/img/left_arrow.svg';
import RightArrow from '../assets/img/right_arrow.svg';

export default function GetStarted(props) {
    let GetStarted = []
    
    for (let i = 0; i < props.getStartedData.length; i++){
        GetStarted.push(
            <Card
                key={i}
                className='card__get-started'
                image={<img src={props.getStartedData[i].image} alt='Body_01' style={{width: 356, height: 266}}/>}
                text={
                    <div>
                        {/* mobile */}
                        <MediaQuery maxWidth={768}>
                            <div className='text__20 text__thai text__bold' style={{margin: 0}}>
                                {props.getStartedData[i].headerTextMobile}
                            </div>
                        </MediaQuery>

                        <MediaQuery maxWidth={768}>
                            <div className='small__text text__thai card__none__interaction'>
                                <span style={{color: '#445DFF', textDecoration: 'underline', fontWeight: 'bold', fontSize: 14}}>รายละเอียดเพิ่มเติม</span>
                            </div>
                        </MediaQuery>

                        {/* PC */}
                        <MediaQuery minWidth={769}>
                            <div className='larger__text' style={{margin: 0}}>
                                {props.getStartedData[i].headerText}
                            </div>
                        </MediaQuery>

                        <MediaQuery minWidth={769}>
                            <div className='smaller__text text__thai' style={{marginTop: 20, marginLeft: 30}}>
                                {props.getStartedData[i].bodyText}
                            </div>
                        </MediaQuery>

                    </div>
                }
                isInteraction={true}
                interaction={
                    <div className='text__18 text__thai'>
                        {props.getStartedData[i].bodyText}
                    </div>
                }
            />
        )
    }

    let [swiper, updateSwiper] = useState(null);
    let [page, setPage] = useState(0); 
    let [isBeginning, setIsBeginning] = useState(true);
    let [isEnd, setIsEnd] = useState(false);

    if (swiper !== null) {
        swiper.on('slideChange', function () {
            setPage(swiper.activeIndex)
            setIsBeginning(swiper.isBeginning)
            setIsEnd(swiper.isEnd)
        });
    }

    const goNext = () => {
        if (swiper !== null) {
            swiper.slideNext();
        }
    };
    
    const goPrev = () => {
        if (swiper !== null) {
            swiper.slidePrev();
        }
    };

	const params = {
		pagination: {
			el: '.carousel__pagination',
			type: 'bullets',
			clickable: true,
			renderBullet: function (index, className) {
				return `<span class="${className}">${index + 1}<br> </span>`;
			},
			bulletClass: "carousel__pagination__button--text",
			bulletActiveClass: "carousel__pagination__button--text--active"
		},
    }

	return (
		<div className='content' style={{paddingBottom: 60}}>
			<div className='larger__text header__text'>
				<div>
					<span className='blue'>เริ่มต้น</span>
					<span className='dark__blue'>อย่างไร?</span>
				</div>
			</div>

            <div className='carousel__container'>
                <Swiper containerClass="carousel__reveser" {...params} getSwiper={updateSwiper}>
                    {GetStarted}
                </Swiper>

                <MediaQuery minWidth={769}>
                    <div 
                        className='carousel__navigation__prev' 
                        style={{display: !isBeginning ? 'flex' : 'none'}} 
                        onClick={goPrev}
                    >
                        <img src={LeftArrow} alt='LeftArrow' style={{marginRight: 5}}/>
                        {page}.
                    </div>

                    <div 
                        className='carousel__navigation__next' 
                        style={{display: !isEnd ? 'flex' : 'none'}} 
                        onClick={goNext}
                    >   
                        {page + 2}.
                        <img src={RightArrow} alt='RightArrow' style={{marginLeft: 5}}/>
                    </div>
                </MediaQuery>

            </div>
		</div>
	)
}