import React from 'react';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'

export default function CustomExpansionPanel(props) {
	const [expanded, setExpanded] = React.useState(false);

  	return (
		<div>
			<ExpansionPanel 
				expanded={expanded} 
				onChange={() => {
					setExpanded(!expanded);
				}} 
				className='expansion__container'
			>
				<ExpansionPanelSummary
					className='expansion__summary'
					expandIcon={<FontAwesomeIcon icon={faChevronDown} className='expansion__arrow'/> }
					aria-controls="panel1bh-content"
					id="panel1bh-header"
				>
					<div className='large__text dark__blue' style={{ margin: 0, fontSize: 26}}>{props.summary}</div>
				</ExpansionPanelSummary>
				<ExpansionPanelDetails 
					className='expansion__detail'
				>
					<div className='expansion__detail__text'>{props.detail}</div>
				</ExpansionPanelDetails>
			</ExpansionPanel>
		</div>
	)
}