import React from 'react';
import { Header, WhyChooseUs, ContactUs, AboutUs } from '../components/Content';
import GetStarted from '../components/GetStarted';
import ExpansionPanel from '../components/ExpansionPanel';
import GetStartedImage1 from '../assets/img/content/get_start_6.png';
import GetStartedImage2 from '../assets/img/content/get_start_2.png';
import GetStartedImage3 from '../assets/img/content/get_start_3.png';
import GetStartedImage4 from '../assets/img/content/get_start_4.png';
import HeaderImage from '../assets/img/header/refinance.png';
import WarningImage from '../assets/img/warning_icon.svg';
import MediaQuery from 'react-responsive';

export default class Refinance extends React.Component{
	componentDidMount() {
		window.scrollTo(0, 0)
	}

	render(){
		const summaryText01 = <span>รีไฟแนนซ์รถคือ?</span>

		const summaryText02 = <span>รีไฟแนนซ์รถได้หรือยัง?</span>

		const detailText01 = 
			<div style={{display: 'flex', flexDirection: 'column'}}>
				<span className='dark__blue'>การย้ายที่ผ่อนรถ โดยปิดบัญชีที่ยังผ่อนอยู่</span>
				<span className='dark__blue'>ด้วยเงินก้อนใหม่ที่กู้จากเรา ด้วยอัตราดอกเบี้ยใหม่</span>
				<span className='dark__blue'>วงเงินใหม่ (ประเมินจากตัวรถปัจจุบัน)</span>
			</div>

		const detailText02 = 
			<div style={{display: 'flex', flexDirection: 'column'}} className='expansion__detail__text__center'>
				<span className='dark__blue'>• ควรผ่อนรถมาแล้ว <font style={{ fontWeight: 'bold'}}>ประมาณครึ่งนึง</font>  ของยอดทั้งหมด</span>
				<span className='dark__blue'>• รถใหม่กว่าปี 2004/2547</span>
				<span className='dark__blue'>• <font style={{ fontWeight: 'bold'}}>ไม่ค้างค่างวด</font>ติดต่อกัน 2 เดือนล่าสุด</span>
			</div>

		return (
			<div className='container'>
				<Header
					slot={
						<div>
							<div className='largest__text'>
								<div>
									<span className='dark__blue'>รีไฟแนนซ์</span>
									<span className='blue'>รถ</span>
								</div>
							</div>

							<img src={HeaderImage} alt='Header' className='header__image'/>
						</div>
					}
				/>

				<MediaQuery maxWidth={768}>
					<ExpansionPanel
						summary={summaryText01}
						detail={detailText01}
					/>
					<ExpansionPanel
						summary={summaryText02}
						detail={detailText02}
					/>
				</MediaQuery>

				<MediaQuery minWidth={769}>
					<div className='duo__text__container'>
						<div className='left__text__container' style={{alignItems: 'flex-end'}}>
							<div>
								<div className='large__text dark__blue'>
									{summaryText01}
								</div>
								<div className='medium__text dark__blue detail__text' style={{marginTop: 30}}>
									{detailText01}
								</div>
							</div>
						</div>
						<div className='right__text__container'>
							<div className='large__text dark__blue'>
								{summaryText02}
							</div>
							<div className='medium__text dark__blue detail__text' style={{marginTop: 30}}>
								{detailText02}
							</div>
						</div>
					</div>
				</MediaQuery>

				<GetStarted
					getStartedData={
						[{
							image: GetStartedImage1,
							headerText:
								<div>
									<span className='dark__blue'>1. เช็คก่อน</span>
									<span className='blue'>สามารถรีไฟแนนซ์ได้หรือยัง?</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>เช็คก่อน สามารถรีไฟแนนซ์ได้หรือยัง?</span>,
							bodyText: 
								<span className='dark__blue'>
									• ควรผ่อนรถมาแล้ว<font style={{ fontWeight: 'bold'}}>ประมาณครึ่งนึง</font>ของยอดทั้งหมด<br></br>
									• รถ<font style={{ fontWeight: 'bold'}}>ใหม่กว่าปี 2004/2547</font><br></br>
									• <font style={{ fontWeight: 'bold'}}>ไม่ค้างค่างวด</font>ติดต่อกัน 2 เดือนล่าสุด
								</span>
						},{
							image: GetStartedImage2,
							headerText:
								<div>
									<span className='dark__blue'>2. </span>
									<span className='blue'>ติดต่อบริษัทไฟแนนซ์รถ</span>
									<span className='dark__blue'>เพื่อสรุปวงเงิน</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>ติดต่อบริษัทไฟแนนซ์รถเพื่อสรุปวงเงิน</span>,
							bodyText: 
								<span className='dark__blue'>
									ติดต่อหาบริษัทไฟแนนซ์รถ<br></br>
									เพื่อประเมินวงเงิน และ ดอกเบี้ยในการจัดไฟแนนซ์รถ <br></br>
									ปัจจัยของวงเงินและดอกเบี้ยจะขึ้นกับ<br></br>
									<font className='text__bold'>รถของคุณ</font>, <font className='text__bold'>วงเงินที่ต้องการ</font>, <font className='text__bold'>รายได้ของผู้จำนำ</font>
								</span>
						},{
							image: GetStartedImage3,
							headerText:
								<div>
									<span className='dark__blue'>3. จัดเตรียม</span>
									<span className='blue'>เอกสาร</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>จัดเตรียมเอกสาร</span>,
							bodyText: 
								<span className='dark__blue'>
									จัดเตรียมเอกสารต่างๆให้พร้อม<br></br>
									เตรียมยื่นให้บริษัทไฟแนนซ์รถ เช่น สมุดทะเบียนรถ,<br></br>
									<font className='text__bold'>บัตรประชาชน</font>, <font className='text__bold'>ทะเบียนบ้าน</font>, <font className='text__bold'>เอกสารแสดงรายได้ย้อนหลัง</font> เป็นต้น<br></br>
									ยิ่งพร้อมเร็ว ยิ่งผ่านเร็วครับ
								</span>
						},{
							image: GetStartedImage4,
							headerText:
								<div>
									<span className='dark__blue'>4. </span>
									<span className='blue'>นัดเซ็นสัญญา </span>
									<span className='dark__blue'>และ</span>
									<span className='blue'>โอนเล่มทะเบียนรถ</span>
								</div>,
							headerTextMobile:
								<span className='dark__blue'>นัดเซ็นสัญญา และ โอนเล่มทะเบียนรถ</span>,
							bodyText: 
								<span className='dark__blue'>
									หลังจากเอกสารพร้อม <font className='text__bold'>บริษัทไฟแนนซ์นัดเซ็นสัญญา</font> เมื่อเซ็นเรียบร้อย<br></br>
									นัดโอนเล่มทะเบียนรถ และรับเงินไปใช้ได้ทันที (ใช้เวลา2-3วัน)
								</span>
						}]
					}
					
				/>
				<WhyChooseUs/>
				<ContactUs 
					warning={
						<span className='warning__text__large'>
							<img src={WarningImage} alt='warning' style={{height: '50%', marginRight: 16}}/>
							<div style={{display: 'flex', flexDirection: 'column'}}>
								<span>• เฉพาะรถยนต์ หรือ รถกระบะ ใหม่กว่าปี 2004 (2547)</span>
								<span>• รถต้องไม่ค้างค่างวด</span>
								<span>• ควรผ่อนมาแล้วประมาณครึ่งหนึ่งของสัญญา</span>
							</div>
						</span>
					}
				/>
				<AboutUs/>
			</div>
		);
	}
}