import React from 'react';
import MenuImage01 from '../assets/img/header/finance.png';
import MenuImage02 from '../assets/img/header/car_exchange.png';
import MenuImage03 from '../assets/img/header/refinance.png';
import { CardButton } from '../components/Card';


export default function Menu() {
	var Menu01 = 
		<CardButton
			to='/usedcarfinance'
			className='card__menu'
			image={<img src={MenuImage01} alt='Body_01' style={{height: '120%'}}/>}
			text={
				<div className='text__30' style={{margin: 0}}>
					<div>
						<span className='dark__blue'>จัดไฟแนนซ์</span>
						<span className='blue'>รถบ้าน</span>
					</div>
					<div>
						<span className='blue'>รถมือสอง</span>
					</div>
				</div>
			}
		/>
	
	var Menu02 = 
		<CardButton
			to='/carforcash'
			className='card__menu'
			image={<img src={MenuImage02} alt='Body_01' style={{height: '120%'}}/>}
			text={
				<div className='text__30' style={{margin: 0}}>
					<div>
						<span className='blue'>รถ</span>
						<span className='dark__blue'>แลก</span>
						<span className='blue'>เงิน</span>
					</div>
					<div>
						<span className='dark__blue'>จำนำทะเบียนรถ</span>
					</div>
				</div>
			}
		/>

		var Menu03 = 
		<CardButton
			to='/carrefinance'
			className='card__menu'
			image={<img src={MenuImage03} alt='Body_01' style={{height: '120%'}}/>}
			text={
				<div className='text__30' style={{margin: 0}}>
					<div>
						<span className='dark__blue'>รีไฟแนนซ์</span>
						<span className='blue'>รถ</span>
					</div>
				</div>
			}
		/>

	return (
		<div className='content' style={{paddingBottom: 50}}>
			<div className='content__inline'>
				{Menu01}
				{Menu02}
				{Menu03}
			</div>
		</div>
	)
}