import React from 'react';
import MediaQuery from 'react-responsive';
import { TextButton, PhoneButton, LineButton } from '../components/Button';
import WhyChooseImage01 from '../assets/img/content/why_choose_1.png';
import WhyChooseImage02 from '../assets/img/content/why_choose_2.png';
import WhyChooseImage03 from '../assets/img/content/why_choose_3.png';
import AboutUsImage from '../assets/img/content/about_us.svg';
import LogoImage from '../assets/img/logo.png';
import FacebookImage from '../assets/img/social/facebook_circle.png';
import LineImage from '../assets/img/social/line_circle.png';
import WarningImage from '../assets/img/warning_icon.svg';
import { Card } from '../components/Card';
import Swiper from 'react-id-swiper';


export function Header(props) {
	return (
		<div className='content' style={props.style}>
			<span className='text__20 blue' style={{marginTop: 40}}>บริการ</span>
			{props.slot}	
		</div>
	)
}

export function WhyChooseUs() {
	var Card01 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage01} alt='Body_01' style={{width: '100%'}}/>}
			text={
				<div className='text__20 text__thai text__bold'>
					<div>
						<span className='dark__blue'>ไม่ใช่นายหน้า</span>
					</div>
					<div>
						<span className='dark__blue'>ค่าธรรมเนียม 0 บาท</span>
					</div>
				</div>
			}
		/>
	
	var Card02 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage02} alt='Body_02' style={{ width: '100%'}}/>}
			text={
				<div className='text__20 text__thai text__bold'>
					<div>
						<span className='dark__blue'>ให้ข้อเสนอที่ดีกว่าที่อื่น</span>
					</div>
					<div>
						<span className='dark__blue'>ไม่เชื่อลองติดต่อดู</span>
					</div>
				</div>
			}
		/>	

	var Card03 = 
		<Card
			className='card__why-choose-us'
			image={<img src={WhyChooseImage03} alt='Body_03' style={{width: '100%'}}/>}
			text={
				<div className='text__20 text__thai text__bold'>
					<div>
						<span className='dark__blue'>พนักงานมีประสปการณ์</span>	
					</div>
					<div>
						<span className='dark__blue' style={{width: '100%'}}>ไม่ต้องต่อสาย ปิดดีลทันที</span>				
					</div>
				</div>
			}
		/>	

	const params = {
		pagination: {
			el: '.carousel__pagination',
			type: 'bullets',
			clickable: true,
			bulletClass: "carousel__pagination__button",
			bulletActiveClass: "carousel__pagination__button--active"
		},
	}

	return (
		<div className='content' style={{paddingBottom: 60}}>
			<div className='scroll__ref' id='why_choose_us'></div>
			<MediaQuery minWidth={768}>
				<div className='larger__text header__text' style={{marginBottom: 30}}>
					<div>
						<span className='dark__blue'>ทำไมต้องเลือก</span>
						<span className='blue'> CAR ATM</span>
					</div>
				</div>
			</MediaQuery>


			<MediaQuery maxWidth={769}>
				<div className='larger__text header__text' style={{marginBottom: 24}}>
					<span className='dark__blue'>ทำไมต้องเลือก CAR ATM</span>
				</div>
			</MediaQuery>

			<MediaQuery minWidth={1024}>
				<div className='content__inline' style={{marginBottom: 30, flexDirection: 'row'}}>
					{Card01}
					{Card02}
					{Card03}
				</div>
			</MediaQuery>

			<MediaQuery maxWidth={1023}>
				<Swiper containerClass="carousel" {...params}>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card01}
					</div>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card02}
					</div>
					<div style={{width: '100vw', maxWidth: 1440}}>
						{Card03}
					</div>
				</Swiper>
			</MediaQuery>
		</div>
	)
}

export function ContactUs(props) {
	let month = (new Date()).getMonth()
	let months = ['ม.ค.', 'ก.พ.', 'มี.ค.', 'เม.ย.', 'พ.ค.', 'มิ.ย.', 'ก.ค.', 'ส.ค.', 'ก.ย.', 'ต.ค.', 'พิ.ย.', 'ธ.ค.']
	let lastDate = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]

	return (
		<div className='content' style={{backgroundColor: '#F2F2F2', maxWidth: 'none'}}>
			<div className='larger__text header__text' style={{marginBottom: 23}}>
				<span className='dark__blue'>จัดไฟแนนซ์รถกับ CAR ATM</span>
			</div>

			<div className='medium__text'>
				<div>
					<span className='dark__blue'>ติดต่อตอนนี้</span>
					<span className='blue'> ให้เงื่อนไขพิเศษ!</span>
				</div>
			</div>	

			<div className='small__text' style={{marginBottom: 15, marginTop: -5}}>
				<span style={{color: '#EB5757'}}>ถึง {lastDate[month]} {months[month]} 19 เท่านั้น</span>
			</div>	
			
			<PhoneButton size={'lg'}/>
			<LineButton size={'lg'} style={{marginTop: 16}} slot={'ติดต่อผ่านไลน์'}/>
			<TextButton 
				slot='ให้เราติดต่อกลับ'
				style={{marginTop: 30, marginBottom: 30}}/>
				
			{props.warning || 
				<span className='warning__text'>
					<img src={WarningImage} alt='warning' style={{height: '40%', marginRight: 8}}/>
					<span>เฉพาะรถยนต์ หรือ รถกระบะ ใหม่กว่าปี 2004 (2547)</span>
				</span>
			}
		</div>
	)
}


export function AboutUs() {
	return (
		<div className='content' style={{maxWidth: 'none' }}>
			<div className='larger__text header__text'>
				<div>
					<span className='blue'>เชื่อใจ</span>
					<span className='dark__blue'>เรา</span>
				</div>
				<div>
					<span className='dark__blue'>ให้บริการสินเชื่อ</span>
					<span className='blue'>กว่า 30 ปี</span>
				</div>
			</div>

			<img src={AboutUsImage} alt='About us' className='about__image'/>
			<img src={LogoImage} alt='Logo' className='about__logo__image'/>

			<Address/>
			<SocialMedia/>
			<Map/>
		</div>
	)
}

export function Map(props) {
	return (
		<iframe 
				title='Google Map'
				style={{width: '100%', height: props.height? props.height : 280}} 
				src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2304.0276929863176!2d100.57365158362776!3d13.785066847663904!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x30e29e8797c1b255%3A0x127774c68d9d47dc!2z4Lia4LiI4LiBLiDguITguKXguLHguIfguYDguKjguKPguKnguJDguIHguLLguKM!5e0!3m2!1sen!2sth!4v1565712215164!5m2!1sen!2sth" 
				frameBorder="0" 
				allowFullScreen
			>
		</iframe>
	)
}

export function SocialMedia(props) {
	return(
		<div style={{margin: 20, ...props.style}}>
			<a href='https://www.facebook.com/caratmthailand/' target="_blank" rel="noopener noreferrer">
				<img src={FacebookImage} alt='Facebook' className='social__image'/>
			</a>
			<a href='https://line.me/R/ti/p/%40189tkxzc' target="_blank" rel="noopener noreferrer">
				<img src={LineImage} alt='Line' className='social__image' style={{marginLeft: 20}}/>
			</a>
		</div>
	)
}

export function Address(props) {
	return(
		<div className='smallest__text' style={props.style}>
			<div>
				<span className='dark__blue'>บริษัท คาร์เอทีเอ็ม จำกัด</span>
			</div>
			<div>
				<span className='dark__blue'>238/16 รัชดาภิเษก ห้วยขวาง กรุงเทพมหานคร 10310</span>
			</div>
			<div>
				<span className='dark__blue'>ติดต่อเรา</span>
				<a href="tel:+6620791081" className='blue'> 02-079-1801</a>
			</div>
		</div>
	)
}